code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.background {
  position: static;
  background-color: #EBEBEB;

}

.heading-home-1 {
  font-size: 40px;
  line-height: 55px;
  text-align: center;
  font-weight: 400;
}

@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}
.row:after {
  content:"";
  display: table;
  clear: both;
}

.left {
  float:left;
  width: 50%;
}
.right {
  float:right;
  width: 50%;
}

.subheading-home-1 {
  padding-top: 20px;
  padding-bottom: 15px;
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  object-fit: fill;
}

.text-block-2 {
  padding-top: 20px;
  font-size: 28px;
  line-height: 50px;
  font-weight: 450;
  text-align: center;
}

.text-block-3 {
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 40px;
  text-align: center;
}

.list-item {
  margin-left: 20px;
  padding-top: 20px;
}

.heading-10 {
  margin-top: 100px;
  padding-bottom:  26px;
  text-align: center;
}

.nav-link {
    padding-top: 25px;
    padding-right: 17px;
    padding-bottom: 26px;
    padding-left: 17px;
    box-shadow: inset 0 3px 0 0 transparent;
    transition-property: box-shadow, color;
    transition-timing-function: ease, ease;
}

.nav-menu {
  position: fixed;
  left: 0px;
  top: 0px;
  right: 0px;
  background-color: white;
  box-shadow: 0 1px 0 0 rgba(0,0,0,0.13);
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 0;
  padding: 4em;
  margin: 0.5em;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}

.section2 {
  background-color: #dde7ed;


}

.sti-info {
  display: flex;
}

.map-marker {
  position: relative;
  display: flex;
  justify-content: center;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  border: 1px solid black;
  background-color: darkorange;
}

.map-marker .map-marker-label {
  display: none;
}

.map-marker:hover {
  cursor: pointer;
  background-color: red;
}

.map-marker:hover .map-marker-label {
  position: absolute;
  display: block;
  white-space: nowrap;
  background-color: white;
  padding: 4px;
  bottom: 16px;
  border-radius: 4px;
  border: 1px solid black;
}

